                                        <img src="~@/assets/images/presale/git.png" class="surprised-logo" />
                                    </a>
                                    <a href="https://discord.com/invite/TuE8JAdHCw" target="_blank">
                                        <img src="~@/assets/images/presale/camre.png" class="surprised-logo" />
                                    </a>
                                    
                                    <a href="https://www.reddit.com/r/ScoutDoge/" target="_blank">
                                        <img src="~@/assets/images/presale/ss.png" class="surprised-logo" />
                                    </a>
                                </div>
                                
                            </div>
                            <div class="sale text-16">Sale Ended</div>
                        </div>
                        <div class="text-white text-18 text-left MT-20">First-ever decentralized gaming platform that attracts people worldwide to play games and earn on the gaming site. The governance token GFC guarantees that the token price will continue to rise until a specified maximum price according to the rebase mechanics.</div>
                    </div>
                </div>
                <div class="border-box sale-list">
                    <el-row v-for="item in dataList" :key="item.label" class="sale-list__item">
                        <el-col :span="8" class="text-left">
                            <div class="text-white text-18 break-word">{{item.label}}</div>
                        </el-col>
                        <el-col :span="16" class="text-right">
                            <div class="text-white text-18 break-word">{{item.value}}</div>
                            <div class="sale-list__desc text-16 MT-10" v-if="item.desc">{{item.desc}}</div>
                        </el-col>
                    </el-row>
                </div>
                <div class="border-box">
                    <el-row class="sale-list__item">
                        <el-col :span="2" class="text-center">
                            <div class="text-white text-18 text-bold break-word">NO.</div>
                        </el-col>
                        <el-col :span="20" class="text-center">
                            <div class="text-white text-18 text-bold break-word">Address (811)</div>
                        </el-col>
                    </el-row>
                    <el-row v-for="(item, index) in tableList" :key="item.label" class="sale-list__item">
                        <el-col :span="2" class="text-center">
                            <div class="text-white text-18 break-word">{{index + 1}}</div>
                        </el-col>
                        <el-col :span="20" class="text-center">
                            <div class="flex-row-center sale-list__content">
                                <div class="text-white text-18 break-word">{{item.content}}</div>
                            </div>
                        </el-col>
                    </el-row>
                    <el-pagination class="MT-10" background layout="prev, pager, next" :total="1000" :pager-count="5"></el-pagination>
                </div>
            </el-col>
            <el-col :xs="24" :sm="24" :md="24" :lg="12" :xl="12">
                <div class="border-box progress" v-loading="buyLoading" element-loading-background="rgba(0, 0, 0, 0.8)">
                    <el-row>
                        <el-col :xs="20" :sm="18" :md="18" :lg="16" :xl="16" class="progress-col">
                            <div class="progress-title text-16">Make sure the website is pinksale.finance!12</div>
                        </el-col>
                    </el-row>
                    <div class="progress-time flex-row-center MT-20 MB-20" v-if="getStatus != 'ended'">
                        <div class="progress-time__item text-20 text-bold MR-20">{{countDownObj.days}}</div>
                        <div class="progress-time__item text-20 text-bold MR-20">{{countDownObj.hours}}</div>
                        <div class="progress-time__item text-20 text-bold MR-20">{{countDownObj.minutes}}</div>
                        <div class="progress-time__item text-20 text-bold">{{countDownObj.seconds}}</div>
                    </div>
                    <el-progress class="MT-20 MB-20" type="circle" :percentage="(soldETH/totalMax)*100" :width="144" :stroke-width="12" :format="format" color="#F5BD02"/>
                    
                    <div class="text-18 text-white">
                        <span class="progress-text__active">{{soldETH}}BNB/</span>
                        <span>{{totalMax}}BNB</span>
                    </div>
                    <div class="text-fff text-16 MT-20">This pool has ended</div>
                    <div class="text-white text-18 MT-30">Amount({{getAmout}})</div>
                    <div class="flex-row-center MT-15">
                        <div class="progress-input flex-row-center">
                            <input type="text" class="text-18 flex-1 text-white" v-model="inputMax" placeholder="0.0">
                            <span class="text-18 progress-input__max pointer" @click="getMax">MAX</span>
                        </div>
                        <div class="progress-buy text-18 ML-20 pointer" @click="buyHandler">BUY</div>
                    </div>
                </div>
                <div class="border-box sale-list">
                    <el-row class="sale-list__item">
                        <el-col :span="12" class="text-left">
                            <div class="text-white text-18 break-word">Status</div>
                        </el-col>
                        <el-col :span="12" class="text-right">
                            <div class="text-white text-18 break-word">{{getStatus}}</div>
                        </el-col>
                    </el-row>
                    <el-row class="sale-list__item">
                        <el-col :span="12" class="text-left">
                            <div class="text-white text-18 break-word">Unsold token</div>
                        </el-col>
                        <el-col :span="12" class="text-right">
                            <div class="text-white text-18 break-word">Scout Dog</div>
                        </el-col>
                    </el-row>
                    <el-row class="sale-list__item">
                        <el-col :span="12" class="text-left">
                            <div class="text-white text-18 break-word">Sale type</div>
                        </el-col>
                        <el-col :span="12" class="text-right">
                            <div class="text-white text-18 break-word">Private</div>
                        </el-col>
                    </el-row>
                    <el-row class="sale-list__item">
                        <el-col :span="12" class="text-left">
                            <div class="text-white text-18 break-word">Minimum Buy</div>
                        </el-col>
                        <el-col :span="12" class="text-right">
                            <div class="text-white text-18 break-word">{{minimum}}BNB</div>
                        </el-col>
                    </el-row>
                    <el-row class="sale-list__item">
                        <el-col :span="12" class="text-left">
                            <div class="text-white text-18 break-word">Maximum Buy</div>
                        </el-col>
                        <el-col :span="12" class="text-right">
                            <div class="text-white text-18 break-word">{{maximum}}BNB</div>
                        </el-col>
                    </el-row>
                    <el-row class="sale-list__item">
                        <el-col :span="12" class="text-left">
                            <div class="text-white text-18 break-word">Total Contributors</div>
                        </el-col>
                        <el-col :span="12" class="text-right">
                            <div class="text-white text-18 break-word">0</div>
                        </el-col>
                    </el-row>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import { DocumentCopy, Select } from '@element-plus/icons'
import { utils } from 'ethers'
import { ElNotification } from 'element-plus'
import moment from 'moment'
export default {
    components: {
        DocumentCopy,
        IconSelect: Select
    },
    data() {
        return {
            dataList: [{
                label: 'Token Name',
                value: 'ScoutDoge' 
            },{
                label: 'Token Symbol',
                value: 'SDOGE' 
            },{
                label: 'Token Decimals',
                value: '9' 
            },{
                label: 'Token Address ',
                value: '0x9E551b430B7Ab145C96c6eDe72a34eC2A90d4eca' ,
                desc: '(Do not send BNB to the token address!)',
            },{
                label: 'Total Supply',
                value: '1,000,000,000,000 SDOGE' 
            },{
                label: 'Tokens For Presale',
                value: '50,000,000,000 SDOGE' 
            },{
                label: 'Tokens For Liquidity',
                value: '32,000,000,000 SDOGE' 
            },{
                label: 'Presale Rate',
                value: '--' 
            },{
                label: 'Listing Rate',
                value: '1 BNB = 80,000,000 SDOGE' 
            },{
                label: 'Soft Cap',
                value: '--' 
            },{
                label: 'Hard Cap',
                value: '--' 
            },{
                label: 'Unsold Tokens',
                value: 'Burn' 
            },{
                label: 'Presale Start Time',
                value: '--' 
            },{
                label: 'Presale End Time',
                value: '--' 
            },{
                label: 'Liquidity Percent',
                value: '80%' 
            },{
                label: 'Liquidity Lockup Time',
                value: '1825 days after pool ends' 
            }],
            tableList: [{
                content: '0xA9b28819cE4354987E5FC532c1cD56A7689E3a3f',
                status: true,
            },{
                content: '0xA9b28819cE4354987E5FC532c1cD56A7689E3a3f',
            },{
                content: '0xA9b28819cE4354987E5FC532c1cD56A7689E3a3f',
            },{
                content: '0xA9b28819cE4354987E5FC532c1cD56A7689E3a3f',
            },{
                content: '0xA9b28819cE4354987E5FC532c1cD56A7689E3a3f',
            },{
                content: '0xA9b28819cE4354987E5FC532c1cD56A7689E3a3f',
            },{
                content: '0xA9b28819cE4354987E5FC532c1cD56A7689E3a3f',
            },{
                content: '0xA9b28819cE4354987E5FC532c1cD56A7689E3a3f',
            }],
            loading: false,
            // 
            originData: {},
            // 
            userData: {},
            // 
            inputMax: 0,
            // 
            userBuyMax: 0,
            buyLoading: false,
            // state
            stateTimer: null,
            // 
            countDownTimer: null,
            // 
            countDownObj: {
                days: '00',
                hours: '00',
                minutes: '00',
                seconds: '00'
            }
        }
    },
    computed: {
        // 
        minimum() {
            return utils.formatEther(this.originData?.purchaseMin || 0)
        },
        // 
        maximum() {
            return utils.formatEther(this.originData?.purchaseMax || 0)
        },
        // 
        totalMax() {
            return utils.formatEther(this.originData?.totalMax || 0)
        },
        // 
        soldETH() {
            return utils.formatEther(this.originData?.SoldETH || 0)
        },
        // 
        getAmout() {
            return utils.formatEther(this.userData?.amount || 0)
        },
        // 
        getStatus() {
            const currentTime = moment().unix();
            const { startTime, stopTime } = this.originData
            if (currentTime < startTime) {
                return 'incoming'
            } else if (currentTime >= startTime && currentTime <= stopTime) {
                return 'inprogress'
            } else {
                return 'ended'
            }
        },
    },
    mounted() {
        this.initData()
        this.getUserInfo()
        // 5State
        this.stateTimer = setInterval(() => {
            this.initData()
        }, 1000 * 60 * 5);
        this.$wallet.on('accountChange', this.getUserInfo)
    },
    watch: {
        inputMax(val) {
            if (isNaN(val)) {
                this.inputMax = this.minimum
            }
            val = Number(val)
            const max = this.userBuyMax || this.maximum
            if (val < this.minimum) {
                this.inputMax = this.minimum
            } else if (val > max) {
                this.inputMax = max
            }
        }
    },
    methods: {
        format() {
            return 'BND'
        },
        // 
        async initData() {
            this.loading = true
            const data = await this.$wallet.preSaleABIOnlyRead.State()
            this.originData = data
            this.inputMax = this.minimum
            const { startTime, stopTime, totalMin, totalMax, ETHPrice} = data;
            const startTime1 = moment(startTime * 1000).format('YYYY.MM.DD HH:mm')
            const endTime = moment(stopTime * 1000).format('YYYY.MM.DD HH:mm')
            this.dataList[8].value = `${utils.formatEther(ETHPrice)}SDOGE`
            this.dataList[9].value = `${utils.formatEther(totalMin)}BNB`
            this.dataList[10].value = `${utils.formatEther(totalMax)}BNB`
            this.dataList[12].value = `${startTime1}UTC`
            this.dataList[13].value = `${endTime}UTC`
            this.loading = false
            this.countDown()
        },
        // 
        countDown() {
            clearInterval(this.countDownTimer)
            const { startTime, stopTime } = this.originData
            let end;
            if (this.getStatus == 'incoming') {
                end = moment(startTime * 1000)
            } else if (this.getStatus == 'inprogress') {
                end = moment(stopTime * 1000)
            }
            if (!end) {
                return
            }
            this.countDownTimer = setInterval(() => {
                const start = moment(new Date());
                const diff = start.diff(end);
                const res = moment.duration(diff)
                this.countDownObj = {
                    days: this.getNumber(res.days()),
                    hours: this.getNumber(res.hours()),
                    minutes: this.getNumber(res.minutes()),
                    seconds: this.getNumber(res.seconds())
                }
            }, 1000)
        },

        getNumber(num) {
            num = Math.abs(num)
            if (num < 10) {
                return '0' + num
            }
            return num
        },

        // 
        async getUserInfo() {
            if (this.$wallet.accounts.length <= 0) {
                return
            }
            this.buyLoading = true
            this.userData = await this.$wallet.preSaleABIOnlyRead.userInfo(this.$wallet.accounts[0])
            const { amount } = this.userData
            const { purchaseMax } = this.originData
            if (amount && purchaseMax) {
                this.userBuyMax = utils.formatEther(purchaseMax.sub(amount))
            }
            this.buyLoading = false
        },
        // 
        async getMax() {
            await this.$wallet.connect()
            await this.getUserInfo()
            this.inputMax = this.userBuyMax;
        },

        // 
        async buyHandler() {
            this.buyLoading = true
            try {
                const network = await this.$wallet.initNetwork()
                const tx = await network.preSaleABI.receivedETH({value: utils.parseEther(this.inputMax)})
                await tx.wait()
                this.buyLoading = false
                this.initData()
                this.getUserInfo()
                ElNotification({
                    title: 'Purchase successful',
                    message: 'Congratulations on your successful purchase',
                    type: 'success',
                })
            } catch (error) {
                this.buyLoading = false
                ElNotification({
                    title: error.code,
                    message: error.message,
                    type: 'error',
                })
            }
        }
    },
    beforeDestroy() {
        clearInterval(this.stateTimer)
        clearInterval(this.countDownTimer)
    },
}
</script>

